export default {
	data() {
		return {
			isMen: false,  
			isWomen: false,
			payType: '',  //支付类型 10为会费支付，20为微信支付，30位线下支付，40为钱包支付
		}
	},
	methods: {
		// 选择性别男
		choiceSex1() {
			this.isMen = !this.isMen
			this.isWomen = false
		},
		// 选择性别女
		choiceSex2() {
			this.isWomen = !this.isWomen
			this.isMen = false
		},
		// 选择支付方式
		choicePay(v) {
			switch(v) {
				case 1:
					this.payType = 10
					break;
				case 2:
					this.payType = 20
					break;
				case 3:
					this.payType = 30
					break;
				case 4:
					this.payType = 40
					break;
			}
		},
		submit() {
			if((!this.isMen) && (!this.isWomen)) {
				uni.showToast({
					title: '请选择性别！',
					duration: 2000,
					icon: 'none'
				});
			}else if(this.payType == ''){
				uni.showToast({
					title: '请选择支付方式！',
					duration: 2000,
					icon: 'none'
				});
			}else{
				
			}
		}
	}
}
